.game {
  /* background-color: #f1f1f1; */
  width: 100%;
  height: 100px;
}

.popup-container {
  position: fixed;
  /* Fixed position to stay in place */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  /* Flexbox for centering */
  justify-content: center;
  /* Center horizontally */
  align-items: center;
  /* Center vertically */
  background-color: rgba(0, 0, 0, 0.5);
  /* Semi-transparent background */
  z-index: 1000;
  /* High z-index to be on top of other content */
}

/* Pop-up content */
.popup-content {
  background: rgb(192, 192, 192);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 70%;
  /* Responsive width */
  height: 80%;
  text-align: center;
}

/* Close button */
.close-button {
  position: absolute;
  top: 9%;
  right: 15%;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #4f4f4f;
}

.close-button:hover {
  color: #A356D6;
}

.board {
  display: grid;
  background-color: #1706ff;
  margin-top: 3%;
  height: 85%;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
}

.board button {
  width: 11%;
  /* Diameter of the circle */
  height: 70px;
  /* Diameter of the circle */
  border-radius: 50%;
  /* Makes the button circular */
  border: none;
  /* Optional: removes the border */
  background-color: #ffffff;
  /* Background color */
  color: white;
  /* Text color */
  text-align: center;
  /* Center the text */
  font-size: 16px;
  /* Font size */
  line-height: 50px;
  /* Aligns text vertically */
  cursor: pointer;
  /* Changes cursor to pointer on hover */
  margin: 5px;
  /* Optional: adds space between buttons */
  outline: none;
}

.board button:hover {
  background-color: #4f4f4f;
}

.board .no-hover:hover {
  cursor: not-allowed;
  background-color: #ffffff;
}

.grid {
  margin-top: 2%;
}

.board .clicked {
  background-color: #ff0000;
  /* or any color you prefer */
}

.board .clicked:hover {
  background-color: #ff0000;
  /* or any color you prefer */
}

.board .placed {
  background-color: #f6ff00;
  /* or any color you prefer */
}

.board .placed:hover {
  background-color: #f6ff00;
  /* or any color you prefer */
}


div[class*=box] {
  height: 33.33%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  line-height: 50px;
  height: 50px;
  text-align: center;
  width: 250px;
  cursor: pointer;
  margin-bottom: 5%;
}

/* 
  ========================
        BUTTON ONE
  ========================
  */
.btn-one {
  color: #FFF;
  transition: all 0.3s;
  position: relative;
}

.btn-one span {
  transition: all 0.3s;
}

.btn-one::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border-top-width: 1px;
  border-bottom-width: 1px;
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-color: rgba(255, 255, 255, 0.5);
  border-bottom-color: rgba(255, 255, 255, 0.5);
  transform: scale(0.1, 1);
}

.btn-one:hover span {
  letter-spacing: 2px;
}

.btn-one:hover::before {
  opacity: 1;
  transform: scale(1, 1);
}

.btn-one::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s;
  background-color: rgba(255, 255, 255, 0.1);
}

.btn-one:hover::after {
  opacity: 0;
  transform: scale(0.1, 1);
}

.gameEnd-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
